@charset "UTF-8";
/*
about
 */
.section.section--about {
  .fp-tableCell {
    display: flex;
    flex-direction: column;
  }
}
.about__wrapper {
  width: 100%;
  padding: 0 140px;
  background: #dfe1e4;
  flex: 0 0 65%;
  @include media(sp) {
    padding: 0 18px;
  }
}
.about {
  display: flex;
  background: #caced1;
  height: 100%;
  padding: 80px 64px 50px 80px;
  @include media(sp) {
    padding: 30px 25px 20px;
  }
  .about__body {
    
    .about__body__infomation {
      margin-bottom: 30px;
    }
    .about__body__contact {
      margin-bottom: 16px;
    }
    .about__contact {
      display: inline-block;
      padding: 12px 26px;
      border: 1px solid #fff;
      color: #fff;
      font-size: 24px;
      font-style: italic;
      letter-spacing: .4em;
      transition: .3s;
      &:hover {
        background: #9cabb7;
      }
    }
  }

  .about__misc {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
    @include media (sp) {
      display: none;
    }
  }
}